'use client';
import { CAREER_PATHS } from '@/lib/careers';
import { useSkills } from '@/hooks/use-skills';
import type { View } from '@/app/page';
import { CheckCircle, Circle, BookOpen, ArrowLeft, MessageSquare } from 'lucide-react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Progress } from '@/components/ui/progress';
import { Badge } from '@/components/ui/badge';

interface CareersProps {
    setCurrentView: (view: View) => void;
    setSelectedCareer: (career: string | null) => void;
    selectedCareer?: string | null;
}

export const Careers = ({ setCurrentView, setSelectedCareer, selectedCareer }: CareersProps) => {
    const { skills } = useSkills();

    const getCareerMatch = (careerName: string) => {
        const career = CAREER_PATHS[careerName as keyof typeof CAREER_PATHS];
        const allSkillsNeeded = career.requiredSkills;
        const matchedSkills = allSkillsNeeded.filter(skill => skills[skill]);
        return { matched: matchedSkills.length, total: allSkillsNeeded.length, percentage: (matchedSkills.length / allSkillsNeeded.length) * 100 };
    };

    if (selectedCareer) {
        const career = CAREER_PATHS[selectedCareer as keyof typeof CAREER_PATHS];
        const match = getCareerMatch(selectedCareer);

        return (
            <div className="space-y-6">
                <Card>
                    <CardHeader>
                        <Button variant="ghost" onClick={() => setCurrentView('careers')} className="justify-start p-0 h-auto mb-4">
                           <ArrowLeft className="mr-2 h-4 w-4" />
                            Back to Career Paths
                        </Button>
                        <CardTitle className="text-3xl font-bold">{selectedCareer}</CardTitle>
                        <CardDescription>{career.description}</CardDescription>
                    </CardHeader>
                    <CardContent>
                         <div className="bg-muted p-4 rounded-lg">
                            <div className="flex justify-between items-center mb-2">
                                <span className="font-semibold text-sm">Your Progress</span>
                                <span className="text-sm text-muted-foreground">{match.matched}/{match.total} skills</span>
                            </div>
                            <Progress value={match.percentage} className="h-2" />
                            <p className="text-sm text-muted-foreground mt-2">{match.percentage.toFixed(1)}% Complete</p>
                        </div>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader>
                        <CardTitle>Required Skills</CardTitle>
                    </CardHeader>
                    <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-3">
                        {career.requiredSkills.map(skill => (
                            <div key={skill} className="flex items-center gap-3 p-3 rounded-lg bg-muted">
                                {skills[skill] ? (
                                    <CheckCircle className="text-green-600" size={20} />
                                ) : (
                                    <Circle className="text-gray-400" size={20} />
                                )}
                                <span className={skills[skill] ? 'font-medium' : 'text-muted-foreground'}>
                                    {skill}
                                </span>
                            </div>
                        ))}
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader>
                        <CardTitle>Learning Roadmap</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="space-y-6">
                            {career.roadmap.map((phase, index) => (
                                <div key={index} className="relative pl-10">
                                    <div className="absolute left-0 top-0 flex items-center justify-center bg-primary text-primary-foreground rounded-full w-8 h-8 font-bold">
                                        {index + 1}
                                    </div>
                                    {index < career.roadmap.length - 1 && <div className="absolute left-4 top-8 bottom-0 w-0.5 bg-border"></div>}
                                    <h3 className="text-xl font-bold mb-1">{phase.phase}</h3>
                                    <span className="text-sm text-muted-foreground mb-3 block">{phase.duration}</span>
                                    <div className="flex flex-wrap gap-2">
                                    {phase.skills.map(skill => (
                                        <Badge key={skill} variant={skills[skill] ? 'default' : 'secondary'}>
                                            {skill}
                                        </Badge>
                                    ))}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader>
                        <CardTitle>Recommended Resources</CardTitle>
                    </CardHeader>
                    <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-3">
                         {career.resources.map((resource, index) => (
                            <div key={index} className="flex items-center gap-3 p-3 rounded-lg bg-muted">
                                <BookOpen className="text-primary" size={20} />
                                <span>{resource}</span>
                            </div>
                        ))}
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader>
                        <CardTitle>Join the Community</CardTitle>
                        <CardDescription>For more details and to connect with others, check out our Discord.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <Button 
                            className="w-full"
                            onClick={() => window.open('https://discord.gg/epWxxeWC', '_blank')}
                        >
                            <MessageSquare className="mr-2 h-4 w-4" />
                            Join Discord
                        </Button>
                    </CardContent>
                </Card>
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <Card>
                 <CardHeader>
                    <Button variant="ghost" onClick={() => setCurrentView('home')} className="justify-start p-0 h-auto mb-4">
                        <ArrowLeft className="mr-2 h-4 w-4" />
                        Back to Home
                    </Button>
                    <CardTitle className="text-3xl font-bold">Career Paths</CardTitle>
                    <CardDescription>Explore different Web3 careers and see how you match</CardDescription>
                </CardHeader>
            </Card>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {Object.entries(CAREER_PATHS).map(([careerName, careerData]) => {
                    const match = getCareerMatch(careerName);
                    return (
                        <Card key={careerName} className="hover:shadow-lg transition-shadow flex flex-col">
                            <CardHeader>
                                <CardTitle>{careerName}</CardTitle>
                                <CardDescription>{careerData.description}</CardDescription>
                            </CardHeader>
                            <CardContent className="flex-grow">
                                <div className="mb-4">
                                    <div className="flex justify-between items-center mb-2">
                                        <span className="text-sm font-semibold">Your Match</span>
                                        <span className="text-sm text-muted-foreground">{match.matched}/{match.total} skills</span>
                                    </div>
                                    <Progress value={match.percentage} className="h-2" />
                                    <p className="text-sm text-muted-foreground mt-1">{match.percentage.toFixed(0)}% Complete</p>
                                </div>
                            </CardContent>
                            <div className="p-6 pt-0">
                                <Button
                                    onClick={() => { setSelectedCareer(careerName); setCurrentView('career-detail'); }}
                                    className="w-full"
                                >
                                    View Roadmap
                                </Button>
                            </div>
                        </Card>
                    );
                })}
            </div>
        </div>
    );
};
