'use client';
import { useSkills } from "@/hooks/use-skills";
import { SKILL_CATEGORIES } from "@/lib/skills";
import { CAREER_PATHS } from "@/lib/careers";
import { TrendingUp, Award, Target, BookOpen, Loader } from "lucide-react";
import type { View } from "@/app/page";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";

interface DashboardProps {
    setCurrentView: (view: View) => void;
}

export const Dashboard = ({ setCurrentView }: DashboardProps) => {
    const { skills, isInitialized } = useSkills();

    const getCategoryProgress = (categoryName: string) => {
        const categorySkills = SKILL_CATEGORIES[categoryName as keyof typeof SKILL_CATEGORIES];
        if (!categorySkills || Object.keys(skills).length === 0) return { checked: 0, total: 0, percentage: 0 };
        const checked = categorySkills.filter(skill => skills[skill]).length;
        return { checked, total: categorySkills.length, percentage: (checked / categorySkills.length) * 100 };
    };

    const getCareerMatch = (careerName: string) => {
        const career = CAREER_PATHS[careerName as keyof typeof CAREER_PATHS];
        if (!career || Object.keys(skills).length === 0) return { matched: 0, total: 0, percentage: 0 };
        const allSkillsNeeded = career.requiredSkills;
        const matchedSkills = allSkillsNeeded.filter(skill => skills[skill]);
        return { matched: matchedSkills.length, total: allSkillsNeeded.length, percentage: (matchedSkills.length / allSkillsNeeded.length) * 100 };
    };

    const totalSkills = Object.keys(skills).length;
    const checkedSkills = Object.values(skills).filter(Boolean).length;
    const overallProgress = totalSkills > 0 ? (checkedSkills / totalSkills) * 100 : 0;

    const bestCareerMatch = Object.keys(CAREER_PATHS).reduce((best, career) => {
        if (!isInitialized) return best;
        const match = getCareerMatch(career);
        const bestMatch = getCareerMatch(best);
        return match.percentage > bestMatch.percentage ? career : best;
    });

    const categoriesMasteredCount = Object.keys(SKILL_CATEGORIES).filter(cat => getCategoryProgress(cat).percentage === 100).length;
    
    if (!isInitialized) {
        return (
            <div className="flex justify-center items-center h-64">
                <Loader className="h-8 w-8 animate-spin" />
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <Card className="bg-gradient-to-r from-primary to-purple-600 text-primary-foreground">
                <CardHeader>
                    <CardTitle className="text-4xl font-bold">Web3 Career Skills Tracker</CardTitle>
                    <CardDescription className="text-xl text-primary-foreground/90">Track your progress and plan your Web3 career journey</CardDescription>
                </CardHeader>
            </Card>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium">Overall Progress</CardTitle>
                        <TrendingUp className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{checkedSkills}/{totalSkills}</div>
                        <p className="text-xs text-muted-foreground">{overallProgress.toFixed(1)}% Complete</p>
                        <Progress value={overallProgress} className="mt-2 h-2" />
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium">Categories Mastered</CardTitle>
                        <Award className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">{categoriesMasteredCount}/{Object.keys(SKILL_CATEGORIES).length}</div>
                        <p className="text-xs text-muted-foreground">Complete skill categories</p>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium">Best Career Match</CardTitle>
                        <Target className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-lg font-bold">{bestCareerMatch}</div>
                        <p className="text-xs text-muted-foreground">
                            {getCareerMatch(bestCareerMatch).percentage.toFixed(0)}% match
                        </p>
                    </CardContent>
                </Card>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Card className="hover:shadow-lg transition-shadow">
                    <CardHeader>
                        <BookOpen className="text-primary mb-2 h-8 w-8" />
                        <CardTitle>All Skills</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <p className="text-muted-foreground mb-4">Browse and track all {totalSkills} Web3 skills organized by category.</p>
                        <Button onClick={() => setCurrentView('all-skills')} className="w-full">View All Skills</Button>
                    </CardContent>
                </Card>

                <Card className="hover:shadow-lg transition-shadow">
                    <CardHeader>
                        <Target className="text-primary mb-2 h-8 w-8" />
                        <CardTitle>Career Paths</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <p className="text-muted-foreground mb-4">Explore different Web3 roles and get personalized roadmaps.</p>
                        <Button onClick={() => setCurrentView('careers')} className="w-full">Explore Careers</Button>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
};
