'use client';
import { SKILL_CATEGORIES } from '@/lib/skills';
import { useSkills } from '@/hooks/use-skills';
import type { View } from '@/app/page';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Progress } from '@/components/ui/progress';
import { Checkbox } from '@/components/ui/checkbox';
import { ArrowLeft, Loader } from 'lucide-react';

interface AllSkillsViewProps {
    setCurrentView: (view: View) => void;
}

export const AllSkills = ({ setCurrentView }: AllSkillsViewProps) => {
    const { skills, toggleSkill, isInitialized } = useSkills();
    const getCategoryProgress = (categoryName: string) => {
        const categorySkills = SKILL_CATEGORIES[categoryName as keyof typeof SKILL_CATEGORIES];
        if (!categorySkills || !isInitialized) return { checked: 0, total: 0, percentage: 0 };
        const checked = categorySkills.filter(skill => skills[skill]).length;
        return { checked, total: categorySkills.length, percentage: (checked / categorySkills.length) * 100 };
    };
    
    if (!isInitialized) {
        return (
            <div className="flex justify-center items-center h-64">
                <Loader className="h-8 w-8 animate-spin" />
            </div>
        );
    }

    return (
        <div className="space-y-6">
            <Card>
                <CardHeader>
                    <Button variant="ghost" onClick={() => setCurrentView('home')} className="justify-start p-0 h-auto mb-4">
                        <ArrowLeft className="mr-2 h-4 w-4" />
                        Back to Home
                    </Button>
                    <CardTitle className="text-3xl font-bold">All Skills</CardTitle>
                    <p className="text-muted-foreground">Track your progress across all {Object.keys(skills).length} Web3 skills</p>
                </CardHeader>
            </Card>

            {Object.entries(SKILL_CATEGORIES).map(([categoryName, categorySkills]) => {
                const progress = getCategoryProgress(categoryName);
                return (
                    <Card key={categoryName}>
                        <CardHeader>
                            <div className="flex justify-between items-center">
                                <CardTitle className="text-2xl">{categoryName}</CardTitle>
                                <div className="flex items-center gap-3">
                                    <span className="text-sm text-muted-foreground">{progress.checked}/{progress.total}</span>
                                    <Progress value={progress.percentage} className="w-24 h-2" />
                                </div>
                            </div>
                        </CardHeader>
                        <CardContent>
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                {categorySkills.map(skill => (
                                    <div key={skill} className="flex items-center space-x-3">
                                        <Checkbox
                                            id={skill}
                                            checked={skills[skill] || false}
                                            onCheckedChange={() => toggleSkill(skill)}
                                        />
                                        <label
                                            htmlFor={skill}
                                            className={`text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 ${skills[skill] ? 'text-muted-foreground line-through' : ''}`}
                                        >
                                            {skill}
                                        </label>
                                    </div>
                                ))}
                            </div>
                        </CardContent>
                    </Card>
                );
            })}
        </div>
    );
};
